      <Navbar />

      <!-- <button @click="show = !show">Toggle show</button> -->
      <div id="banner">
        <div id="banner2">
          <transition name="fadeIn">
            <div id="banner3" v-if="show" @click="rollStart()">
              DONALD ALBERTI

              <div id="bio">
                (1950-) Reductive Abstraction and Color Painting
              </div>
            </div>
          </transition>
        </div>
      </div>
    </div>

    <router-view
      :inventory="filteredFinal2"
      :listOfSupports="listOfSupports"
      :listOfMediums="listOfMediums"
      :listOfSeries="listOfSeries"
      :arr="arr"
      :filteredFinal="filteredFinal"
      :listOfRed="listOfReds"
      :listOfReds="listOfRed"
      :filteredFinall="filteredFinall"
      :popInv="inv"
      :filteredFinalll="filteredFinalll"
      :filteredFinalll3="filteredFinalll3"
      :filteredFinall2="filteredFinall2"
    />
