<script>
import * as d3 from "d3";
// import Services from "./components/Services";

import Navbar from "@/components/navBar.vue/";
import axios from "axios";
///

export default {
  components: {
    Navbar,
  },
  created() {
    this.rollStart();
  },
  mounted() {
    // Promise.all([d3.csv("DWAwork.csv")]).then((data) => {
    //   this.inventory = data[0];
    //   console.log(this.inventory);
    // });
    this.getData(this.api.baseUrl);
    Promise.all([
      d3.csv(
        "https://docs.google.com/spreadsheets/d/e/2PACX-1vSA5e_7nmGhNbFxkxiVjJVvW-rMXG56Sln5svAJHAnemyyOcHvqrfAjsr12ZbLlTQbnG_vojWZ830GE/pub?output=csv"
      ),
    ]).then((data) => {
      this.inventory = data[0];
      console.log(this.inventory);
    });

    //
  },
  data() {
    return {
      inventory: [],
      show: true,
      arr: null,
      questions: [],
      api: {
        baseUrl: `https://sheets.googleapis.com/v4/spreadsheets/1p3rpbnXvfFdShR3CJmJ4fhWcZJnHm-p3u3ccbm8-mb0/values/DWAwork?alt=json&key=`,
      },

      //
      inv: [],
    };
  },
  computed: {
    listOfSupports() {
      return Array.from(new Set(this.inventory.map((d) => d.support)));
    },
    listOfMediums() {
      return Array.from(new Set(this.inventory.map((d) => d.medium)));
    },
    listOfSeries() {
      return Array.from(new Set(this.inventory.map((d) => d.series)));
    },
    listOfReds() {
      return Array.from(new Set(this.inventory.map((d) => d.red)));
    },
    listOfRed() {
      if (!this.inventory) {
        return null;
      }
      let m = this.inventory.filter(
        (d) => d.imgname !== "99" && d.imgname !== "98" && d.red == 1
      );
      return m.sort((a, b) => (a.series > b.series ? 1 : -1));
    },
    filteredFinal2() {
      let m = this.inventory.filter(
        (d) => d.imgname !== "99" && d.imgname !== "98"
      );
      return m.sort((a, b) => (a.year > b.year ? 1 : -1));
    },
    filteredFinal() {
      if (!this.inventory) {
        return null;
      }
      let m = this.inventory.filter(
        (d) =>
          d.imgname !== "99" &&
          d.imgname !== "98" &&
          d.imgname !== "95" &&
          d.imgname !== "93" &&
          d.imgname !== "94" &&
          d.imgname !== "92" &&
          d.series.includes("Maquettes")
      );
      return m.sort((a, b) => (a.series > b.series ? 1 : -1));
    },
    filteredFinall() {
      let m = this.inventory.filter(
        (d) =>
          (d.imgname !== "99" &&
            d.imgname !== "98" &&
            d.support.includes("canvas")) ||
          d.support.includes("marine board") ||
          d.support.includes("canvas + linen") ||
          d.support.includes("honeycomb fibreglass") ||
          d.support.includes("fibreglass") ||
          d.support.includes("aluminum")
      );
      // return m.sort((a, b) => (a.year > b.year ? 1 : -1));
      // return m.sort((a, b) => (a.id > b.id ? 1 : -1));
      function groupBy(list, keyGetter) {
        const map = new Map();
        list.forEach((item) => {
          const key = keyGetter(item);
          const collection = map.get(key);
          if (!collection) {
            map.set(key, [item]);
          } else {
            collection.push(item);
          }
        });

        return map;
      }
      var grouped = groupBy(m, (d) => d.series);

      let array = Array.from(grouped, ([name, value]) => ({
        name,
        value,
      }));

      return array;
    },
    filteredFinall2() {
      let m = this.inventory.filter(
        (d) =>
          d.imgname !== "99" &&
          d.imgname !== "98" &&
          d.support.includes("paper")
      );
      // return m.sort((a, b) => (a.year > b.year ? 1 : -1));
      // return m.sort((a, b) => (a.id > b.id ? 1 : -1));
      function groupBy(list, keyGetter) {
        const map = new Map();
        list.forEach((item) => {
          const key = keyGetter(item);
          const collection = map.get(key);
          if (!collection) {
            map.set(key, [item]);
          } else {
            collection.push(item);
          }
        });

        return map;
      }
      var grouped = groupBy(m, (d) => d.series);

      let array = Array.from(grouped, ([name, value]) => ({
        name,
        value,
      }));

      return array;
    },
    filteredFinalll() {
      let m = this.inventory.filter(
        (d) =>
          (d.imgname !== "99" &&
            d.imgname !== "98" &&
            d.support.includes("canvas")) ||
          d.support.includes("marine board") ||
          d.support.includes("canvas + linen") ||
          d.support.includes("honeycomb fibreglass") ||
          d.support.includes("fibreglass") ||
          d.support.includes("aluminum")
      );
      return m.sort((a, b) => (a.year > b.year ? 1 : -1));
    },
    filteredFinalll3() {
      let m = this.inventory.filter(
        (d) =>
          d.imgname !== "99" &&
          d.imgname !== "98" &&
          d.support.includes("paper")
      );
      return m;
    },
  },
  methods: {
    rollStart() {
      this.arr = [];
      while (this.arr.length < 3) {
        var r = Math.floor(Math.random() * 10) + 1;
        if (this.arr.indexOf(r) === -1) this.arr.push(r);
      }
      console.log(process.env.VUE_APP_API_KEY);
      console.log(this.arr);
    },
    getData(apiUrl) {
      axios.get(apiUrl).then((res) => {
        this.questions = res.data.values;
        console.log(this.questions);
        // console.log(this.questions[0]);
      });

      // .catch(error => console.log(error));
    },
  },
};
</script>
